<template>
  <ion-page>
    <ion-header mode="ios">
      <ion-toolbar>
        <ion-title>Hasil Ujian</ion-title>
        <ion-back-button slot="start" default-href="/" />
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
      <ion-refresher slot="fixed" @ionRefresh="doRefresh($event)">
        <ion-refresher-content />
      </ion-refresher>
      <ion-content class="ion-padding">
        <ion-grid>
          <ion-row>
            <ion-col>
              <h2>Daftar Hasil Ujian</h2>
              <ion-list>
                <ion-item>
                  <ion-label>
                    <table>
                      <thead>
                        <tr>
                          <th>Mata Pelajaran</th>
                          <th>Nilai</th>
                          <th>Tanggal</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="result in examResults" :key="result.id">
                          <td>{{ result.mataPelajaranName }}</td>
                          <td>
                            <ion-badge
                              :color="result.score < 65 ? 'danger' : 'success'"
                            >
                              {{ result.score }}
                            </ion-badge>
                          </td>
                          <td>{{ formatDate(result.createdAt) }}</td>
                        </tr>
                      </tbody>
                    </table>
                  </ion-label>
                </ion-item>
              </ion-list>
            </ion-col>
          </ion-row>
        </ion-grid>
      </ion-content>
      <ion-toast
        :is-open="toast.isOpen"
        :message="toast.message"
        :duration="toast.duration"
        :color="toast.color"
        @did-dismiss="() => (toast.isOpen = false)"
      ></ion-toast>
    </ion-content>
  </ion-page>
</template>

<script setup>
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonBackButton,
  IonContent,
  IonRefresher,
  IonRefresherContent,
  IonGrid,
  IonList,
  IonItem,
  IonLabel,
  IonToast,
  IonBadge,
} from "@ionic/vue";
import { ref, onMounted } from "vue";
import {
  getFirestore,
  collection,
  query,
  getDocs,
  doc,
  getDoc,
} from "firebase/firestore";
import { getAuth } from "firebase/auth";
import { firebaseApp } from "../firebase.js";

const db = getFirestore(firebaseApp);
const auth = getAuth(firebaseApp);
const examResults = ref([]);
const toast = ref({
  isOpen: false,
  message: "",
  duration: 2000,
  color: "success",
});

const fetchExamResults = async () => {
  const user = auth.currentUser;
  if (user) {
    try {
      const q = query(collection(db, `examResults/${user.uid}/results`));
      const querySnapshot = await getDocs(q);

      const results = await Promise.all(
        querySnapshot.docs.map(async (docSnapshot) => {
          const data = docSnapshot.data();
          console.log("Exam data:", data); // Debugging log
          const jadwalDoc = await getDoc(doc(db, "jadwal", data.jadwalId));
          if (jadwalDoc.exists()) {
            const jadwalData = jadwalDoc.data();
            console.log("Jadwal data:", jadwalData); // Debugging log
            const mataPelajaranDoc = await getDoc(
              doc(db, "mataPelajaran", jadwalData.mataPelajaran)
            );
            if (mataPelajaranDoc.exists()) {
              data.mataPelajaranName = mataPelajaranDoc.data().name;
              console.log("Mata Pelajaran data:", mataPelajaranDoc.data()); // Debugging log
            }
          }
          return { id: docSnapshot.id, ...data };
        })
      );

      examResults.value = results;
      console.log("Hasil ujian:", examResults.value); // Debugging log
    } catch (error) {
      console.error("Error fetching exam results:", error);
      showToast("Terjadi kesalahan saat mengambil data hasil ujian.", "danger");
    }
  } else {
    showToast("Pengguna tidak terautentikasi.", "danger");
  }
};

const formatDate = (timestamp) => {
  if (!timestamp) return "N/A";
  const date = new Date(timestamp.seconds * 1000);
  return date.toLocaleDateString("id-ID", {
    year: "numeric",
    month: "short",
    day: "numeric",
  });
};

const showToast = (message, color) => {
  toast.value = {
    isOpen: true,
    message,
    duration: 2000,
    color,
  };
};

const doRefresh = (event) => {
  setTimeout(() => {
    fetchExamResults();
    event.target.complete();
  }, 2000);
};

onMounted(() => {
  fetchExamResults();
});
</script>

